@import url(https://fonts.googleapis.com/css?family=Montserrat:300,500,700&display=swap);
@charset "UTF-8"; /* https://dev.to/hankchizljaw/a-modern-css-reset-6p3 */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul[class],
ol[class] {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul[class],
ol[class] {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    -webkit-transition-duration: 0.01ms !important;
            transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.elevation-level-1 {
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.5);
}

.elevation-level-2 {
  box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.5);
}

.elevation-level-3 {
  box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.5);
}

.elevation-level-4 {
  box-shadow: 0px 1px 20px 0px rgba(0, 0, 0, 0.5);
}

.elevation-level-5 {
  box-shadow: 0px 1px 25px 0px rgba(0, 0, 0, 0.5);
}

.elevation-level-6 {
  box-shadow: 0px 1px 30px 0px rgba(0, 0, 0, 0.5);
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    z-index: -1;
  }
  100% {
    opacity: 1;
    z-index: 999;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    z-index: -1;
  }
  100% {
    opacity: 1;
    z-index: 999;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
    z-index: 999;
  }
  100% {
    opacity: 0;
    z-index: -999;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
    z-index: 999;
  }
  100% {
    opacity: 0;
    z-index: -999;
  }
}
