@media screen and (min-width: 360px) and (max-width: 539px) {
  #suppliers-container {
    grid-template-columns: 1fr;
  }
  #suppliers-container .supplier.expanded {
    grid-column: span 1;
    grid-row: span 2;
  }
  .supplier-content-header-logo {
    width: 60% !important;
  }
  .supplier-content-main .supplier-product-carousel-container {
    max-height: 125px !important;
  }
}
@media screen and (min-width: 540px) and (max-width: 767px) {
  #suppliers-container {
    grid-template-columns: 1fr 1fr;
  }
  #suppliers-container .supplier.expanded {
    grid-column: span 2;
    grid-row: span 2;
  }
  #landing-content-container h1 {
    padding-top: 30px;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 24px;
  }
  #landing-content-container h1 {
    padding: 30px;
  }
  #contact * {
    font-size: 0.8rem;
  }
  #about-content-container {
    width: 80%;
  }
}
@media screen and (min-width: 768px) and (max-width: 991px) {
  #suppliers-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
  #suppliers-container .supplier.expanded {
    grid-column: span 3;
    grid-row: span 2;
  }
  #contact-form input[type=submit] {
    width: 50%;
  }
  #contact-panels {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto auto auto;
    grid-template-areas: "email-message visit" "hours visit";
  }
}
@media screen and (min-width: 992px) and (max-width: 1199px) {
  #suppliers-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
  #suppliers-container .supplier.expanded {
    grid-column: span 3;
    grid-row: span 2;
  }
  #contact-form input[type=submit] {
    width: 50%;
  }
  #contact-panels {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: auto;
    grid-template-areas: "email-message visit visit" "hours visit visit";
  }
}
@media screen and (min-width: 1200px) {
  #suppliers-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  #suppliers-container .supplier.expanded {
    grid-column: span 2;
    grid-row: span 2;
  }
  #contact-form input[type=submit] {
    width: 50%;
  }
  #contact-panels {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: auto;
    grid-template-areas: "email-message visit visit" "hours visit visit";
    justify-items: stretch;
    justify-content: stretch;
  }
}
body {
  margin: 0;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(55, 55, 55) !important !important;
  font-weight: 300;
  color: #6e7271;
  min-width: 320px;
}
body * {
  font-family: inherit;
  z-index: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  text-align: center;
}

p {
  margin: 0px;
}

a {
  text-decoration: none;
  color: #6e7271;
}
a:visited {
  color: inherit;
}
a.selected {
  color: inherit;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url(data:image/gif;base64,) center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url(../../static/media/slick.ced611da.eot);
  src: url(../../static/media/slick.ced611da.eot) format("embedded-opentype"), url(../../static/media/slick.b7c9e1e4.woff) format("woff"), url(../../static/media/slick.d41f55a7.ttf) format("truetype"), url(../../static/media/slick.f97e3bbf.svg) format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}
.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}
[dir=rtl] .slick-prev {
  left: auto;
  right: -25px;
}
.slick-prev:before {
  content: "←";
}
[dir=rtl] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}
[dir=rtl] .slick-next {
  left: -25px;
  right: auto;
}
.slick-next:before {
  content: "→";
}
[dir=rtl] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}
.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}
.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}
